.App {
  $cal-font-size: calc(100vw / 12);
  margin-top: 30px;
  padding: 20px;
  border-radius: 10px;
  padding: 20px;
  width: auto;
  background-color: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  color: cornflowerblue;
  .appTitle {
    display: flex;
    flex-flow: row;
    width: 100%;
    table {
      width: 100%;
      border-spacing: 0px;
      td {
        text-align: left;
        padding: 5px;
        border: 1px solid black;
        font-size: calc($cal-font-size / 2);
        color: black;
      }
      #right-count {
        color: green;
      }
      #wrong-count {
        color: red;
      }
    }
  }

  #refresh-btn {
    height: $cal-font-size;
    margin-top: 5px;
    border: 1px solid rgb(83, 161, 5);
    // background-image: linear-gradient(rgb(198, 248, 124), rgb(178, 248, 74), rgb(207, 247, 148));
    background-color: cadetblue;
  }

  .cal-item {
    font-size: $cal-font-size;
    display: flex;
    flex-flow: row;
    text-align: left;
    align-items: center;
    .cal-op {
      display: flex;
      flex-direction: row;
      margin-top: 10px;
      .op-item {
        width: $cal-font-size;
      }
      .op {
        text-align: center;
        width: $cal-font-size;
        color: darkblue;
      }
      .op-ret {
        font-size: $cal-font-size;
        width: calc($cal-font-size * 1.5);
        color: rgb(175, 5, 197);
      }
      .op-right {
        color: green;
      }
      .op-wrong {
        color: red;
      }
      .op-right-ret {
        margin-left: $cal-font-size;
        font-size: calc($cal-font-size/2);
        color: green;
      }
    }
    .cal-right-ret {
      font-size: calc($cal-font-size / 2);
      color: rgb(0, 255, 0);
    }
  }
}
